import { createWebHistory, createRouter } from "vue-router";
import Home from "./components/page/Home.vue";
import Font from "./components/page/Font.vue";
import Image from "./components/page/Image.vue";
import Icon from "./components/page/Icon.vue";
import Pattern from "./components/page/Pattern.vue";
import Mockup from "./components/page/Mockup.vue";
import Color from "./components/page/Color.vue";



const routes = [
  {
    path: "/color", //경로 설정
    component: Color, //위에서 import해온 컴포넌트,
  },
  {
    path: "/free-pattern", //경로 설정
    component: Pattern, //위에서 import해온 컴포넌트,
  },
  {
    path: "/free-mockup", //경로 설정
    component: Mockup, //위에서 import해온 컴포넌트,
  },
  {
    path: "/free-icon", //경로 설정
    component: Icon, //위에서 import해온 컴포넌트,
  },
  {
    path: "/free-image", //경로 설정
    component: Image, //위에서 import해온 컴포넌트,
  },
  {
    path: "/free-font", //경로 설정
    component: Font, //위에서 import해온 컴포넌트,
  },
  {
    path: "/", //경로 설정
    component: Home, //위에서 import해온 컴포넌트,
  },
];

const scrollBehavior = function (){
  return {top:0};
}

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior,
});

export default router; 