<template>
  <section id="gnb" :class="{changeBG: scrollPosition > 10}">
    <div class="menu-detail mobile" v-if="show == true" @click="openMenu();">
      <div class="menu-wrap" @click="show = false;">
        <button v-for=" i in 메뉴" :key="i">
          <router-link :to="i.link">{{i.메뉴명}}</router-link>
        </button>
      </div>
      <Footer class="white" />
    </div>
    <div class="gnb-wrap">
      <div class="logo" @click="show = false" :class="{white : show}">
        <router-link to="/">
          <svg width="38" height="38" viewBox="0 0 38 38" xmlns="http://www.w3.org/2000/svg">
            <path d="M0.5 9.5L12.3 9.5L12.3 12.3L0.5 12.3L0.5 9.5Z"/>
            <path fill-rule="evenodd" clip-rule="evenodd" d="M0.5 0.5H37.5V37.5H0.5V18.5H12.2581V21.3462H3.17442V34.6538H34.8256V3.34615H0.5V0.5Z"/>
            <path fill-rule="evenodd" clip-rule="evenodd" d="M15.7998 9.5H25.2998C28.5583 9.5 31.1998 12.1415 31.1998 15.4C31.1998 17.6653 29.9232 19.6315 28.0517 20.6201L31.2819 27.8881L28.7232 29.0253L25.2898 21.3H18.5998V29.4H15.7998V9.5ZM18.5998 12.3V18.5H25.2998C27.0119 18.5 28.3998 17.1121 28.3998 15.4C28.3998 13.6879 27.0119 12.3 25.2998 12.3H18.5998Z"/>
          </svg>
        </router-link>
      </div>
      <div class="menu">
        <button v-for=" i in 메뉴" :key="i" class="web">
          <router-link :to="i.link">{{i.메뉴명}}</router-link>
        </button>
        <div class="mobile" @click="show = !show; openMenu()">
          <button class="icon" :class="{close : show}" >
            <span class="line1"></span>
            <span class="line2"></span>
          </button>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
    name : "Gnb",
    data() {
        return {
            메뉴 : [
            {메뉴명:'무료 폰트', link:'/free-font'},
            {메뉴명:'무료 이미지', link:'/free-image'},
            {메뉴명:'무료 아이콘', link:'/free-icon'},
            {메뉴명:'무료 패턴', link:'/free-pattern'},
            {메뉴명:'무료 목업', link:'/free-mockup'},
            {메뉴명:'색상조합', link:'/color'},
            ],
            scrollPosition : null,
            show : false,
        }
    },
    methods: {
        updateScroll(){
            this.scrollPosition = window.scrollY;
        },
        openMenu(){
            if(this.show){
                document.querySelector("body").classList.add("overflow-hidden");
                document.querySelector("#app").classList.add("overflow-hidden");
            }else {
                document.querySelector("body").classList.remove("overflow-hidden");
                document.querySelector("#app").classList.remove("overflow-hidden");
            }
        }
    },
    mounted() {
        window.addEventListener('scroll', this.updateScroll);
    },
}
</script>

